import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({
  stepperContainer: {
    [theme.breakpoints.down('lg')]: {
      width: theme.breakpoints.values.sm,
    },
    [theme.breakpoints.up('lg')]: {
      width: theme.breakpoints.values.smd,
    },
    [theme.breakpoints.up('sm')]: {
      borderRadius: theme.spacing(1),
      marginTop: theme.spacing(3),
    },
    marginBottom: theme.spacing(6),
    // backgroundColor: theme.palette.mode === 'dark' ? grey[800] : grey[100],
  },
  stepperHeader: {
    margin: theme.spacing(3),
    [theme.breakpoints.between('sm', 'lg')]: {
      width: `calc(${theme.breakpoints.values.sm}px - ${theme.spacing(6)})`,
    },
    [theme.breakpoints.up('lg')]: {
      width: `calc(${theme.breakpoints.values.smd}px - ${theme.spacing(6)})`,
    },
  },
  cardContainer: {
    width: 360,
    height: 200,
    backgroundColor: theme.palette.mode === 'dark' ? grey[700] : grey[200],
  },
  stepperContent: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
  },
  completeButton: {
    marginTop: theme.spacing(2),
  },
  iconButton: {
    marginTop: theme.spacing(2.5),
  },
  stepperInnerContent: {
    margin: theme.spacing(1),
  },
}));

const CardDetails = () => {
  const [data, setData] = useState({
    frontCard: front,
    backCard: back,
  });
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState({});
  const [frontCard, setFrontCard] = useState();
  const [frontCardIsLoading, setFrontCardIsLoading] = useState(false);
  const [previewFrontCard, setPreviewFrontCard] = useState();
  const [frontCardIsValid, setFrontCardIsValid] = useState(false);
  const [backCard, setBackCard] = useState();
  const [backCardIsLoading, setBackCardIsLoading] = useState(false);
  const [previewBackCard, setPreviewBackCard] = useState();
  const [backCardIsValid, setBackCardIsValid] = useState(false);
  const [contactInfoIsValid, setContactInfoIsValid] = useState(false);
  const [companyInfoIsValid, setCompanyInfoIsValid] = useState(false);
  const [cardInfoIsValid, setCardInfoIsValid] = useState(true);
  const [linksInfoIsValid, setLinksInfoIsValid] = useState(true);
  const [DetailsInfoIsValid, setDetailsInfoIsValid] = useState(true);

  const frontCardRef = useRef();

  const backCardRef = useRef();

  const [contactForm, setContactForm] = useState({
    contactName: {
      value: '',
      name: 'contact-name',
      label: 'Contact Name',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 6,
        maxLength: 10,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    contactNumber: {
      value: '',
      name: 'contact-number',
      label: 'Contact Number',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    contactEmail: {
      value: '',
      name: 'contact-email',
      label: 'Contact Email',
      type: 'email',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        isEmail: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    smsBody: {
      value: '',
      name: 'sms-body',
      label: 'SMS Body',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });

  const [companyForm, setCompanyForm] = useState({
    companyName: {
      value: '',
      name: 'company-name',
      label: 'Company Name',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    role: {
      value: '',
      name: 'role',
      label: 'Role',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    category: {
      value: '',
      name: 'category',
      label: 'Category',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    tags: {
      value: '',
      name: 'tags',
      label: 'Tags',
      type: 'select',
      componentType: 'Autocomplete',
      placeHolder: '',
      multiple: true,
      options: [
        'option1',
        'option2',
        'option3',
        'option4',
        'option5',
        'option6',
        'option7',
        'option8',
      ],
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    website: {
      value: '',
      name: 'websiteLink',
      label: 'Website Link',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    address: {
      value: '',
      name: 'address',
      label: 'Address',
      type: 'text',
      placeHolder: '',
      multiline: true,
      disabled: false,
      validations: {
        isRequired: true,
        minLength: 1,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });

  const [newLink, setNewLink] = useState({
    newTitle: {
      value: '',
      name: 'new-link-title',
      label: 'New Link Title',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    newUrl: {
      value: '',
      name: 'new-link-url',
      label: 'New Link URL',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });

  const linksMapping = {
    newTitle: 'title',
    newUrl: 'url',
  };

  const [links, setLinks] = useState([
    {
      title: {
        value: 'Like me on Facebook',
        name: 'link-title',
        label: 'Link Title',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
      url: {
        value: 'www.facebook.com',
        name: 'link-url',
        label: 'Link URL',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
    },
    {
      title: {
        value: 'Subscribe me on Youtube',
        name: 'link-title',
        label: 'Link Title',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
      url: {
        value: 'www.youtube.com',
        name: 'link-url',
        label: 'Link URL',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
    },
  ]);

  const [newDetail, setNewDetail] = useState({
    newTitle: {
      value: '',
      name: 'new-detail-title',
      label: 'New Detail Title',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    newDescription: {
      value: '',
      name: 'new-detail-description',
      label: 'New Detail Description',
      type: 'text',
      placeHolder: '',
      multiline: true,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });

  const detailsMapping = {
    newTitle: 'title',
    newDescription: 'description',
  };

  const [details, setDetails] = useState([
    {
      title: {
        value: 'ipsum',
        name: 'detail-title',
        label: 'Detail Title',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
      description: {
        value: 'ipsum',
        name: 'detail-description',
        label: 'Detail Description',
        type: 'text',
        placeHolder: '',
        multiline: true,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
    },
    {
      title: {
        value: 'ipsum',
        name: 'detail-title',
        label: 'Detail Title',
        type: 'text',
        placeHolder: '',
        multiline: false,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
      description: {
        value: 'ipsum',
        name: 'detail-description',
        label: 'Detail Description',
        type: 'text',
        placeHolder: '',
        multiline: true,
        disabled: false,
        validations: {
          isRequired: true,
        },
        isTouched: false,
        validity: { isValid: true, label: '*Required' },
        icon: {
          type: 'close',
          close: closeCircleIcon,
        },
      },
    },
  ]);

  const [state, dispatch] = useStore(true);

  const isSmallDevice = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  const isMediumDevice = useMediaQuery((theme) =>
    theme.breakpoints.between('sm', 'lg')
  );
  const isLargeDevice = useMediaQuery((theme) => theme.breakpoints.up('lg'));
  const classes = useStyles();

  useEffect(() => {
    if (!frontCard) {
      return;
    }
    const fileReader = new FileReader();
    fileReader.onload = () => {
      setPreviewFrontCard(fileReader.result);
    };
    fileReader.readAsDataURL(frontCard);
  }, [frontCard]);

  useEffect(() => {
    if (!backCard) {
      return;
    }
    const fileReader = new FileReader();
    fileReader.onload = () => {
      setPreviewBackCard(fileReader.result);
    };
    fileReader.readAsDataURL(backCard);
  }, [backCard]);

  const pickCardHandler = (cardFacade) => () => {
    cardFacade === 'front'
      ? setFrontCardIsLoading(true)
      : setBackCardIsLoading(true);

    cardFacade === 'front'
      ? frontCardRef.current.click()
      : backCardRef.current.click();
  };

  const contactObjectListCompleteHandler = useCallback((args) => {
    const { objectList } = { ...args };

    const updatedForm = [...objectList];

    const formValidityList = [];

    updatedForm.map((item) => {
      for (let key in item) {
        formValidityList.push(item[key].validity.isValid);
      }
      return 0;
    });

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setLinksInfoIsValid(formIsValid);
  }, []);

  useEffect(() => {
    contactObjectListCompleteHandler({ objectList: links });
  }, [links, contactObjectListCompleteHandler]);

  const detailObjectListCompleteHandler = useCallback((args) => {
    const { objectList } = { ...args };

    const updatedForm = [...objectList];

    const formValidityList = [];

    updatedForm.map((item) => {
      for (let key in item) {
        formValidityList.push(item[key].validity.isValid);
      }
      return 0;
    });

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setDetailsInfoIsValid(formIsValid);
  }, []);

  useEffect(() => {
    detailObjectListCompleteHandler({ objectList: details });
  }, [details, detailObjectListCompleteHandler]);

  const contactFormCompleteChangeHandler = useCallback((args) => {
    const { formObject } = { ...args };

    const updatedForm = { ...formObject };

    const formValidityList = [];

    for (let key in updatedForm) {
      formValidityList.push(updatedForm[key].validity.isValid);
    }

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setContactInfoIsValid(formIsValid);
  }, []);

  useEffect(() => {
    contactFormCompleteChangeHandler({ formObject: contactForm });
  }, [contactForm, contactFormCompleteChangeHandler]);

  const companyFormCompleteChangeHandler = useCallback((args) => {
    const { formObject } = { ...args };

    const updatedForm = { ...formObject };

    const formValidityList = [];

    for (let key in updatedForm) {
      formValidityList.push(updatedForm[key].validity.isValid);
    }

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setCompanyInfoIsValid(formIsValid);
  }, []);

  useEffect(() => {
    companyFormCompleteChangeHandler({ formObject: companyForm });
  }, [companyForm, companyFormCompleteChangeHandler]);

  const fieldChangeHandler = useCallback((args, event) => {
    const { fieldName, formObject, changeType = 'change' } = { ...args };

    let updatedForm = {
      ...formObject,
      [fieldName]: {
        ...formObject[fieldName],
        value: changeType === 'change' ? event.target.value : '',
        validity: {
          ...formObject[fieldName].validity,
          ...fieldValidation(
            changeType === 'change' ? event.target.value : '',
            formObject[fieldName].validations
          ),
        },
        isTouched: true,
      },
    };
    return updatedForm;
  }, []);

  const detailsFormObjectListChangeHandler = useCallback(
    (args) => (event) => {
      const { objectList, index } = { ...args };

      let updatedForm = fieldChangeHandler(args, event);

      let arrayObjectForm = [...objectList];
      let updatedList = { ...arrayObjectForm[index] };
      updatedList = updatedForm;
      arrayObjectForm[index] = updatedList;

      updatedForm = arrayObjectForm;

      setDetails(updatedForm);
    },
    [fieldChangeHandler]
  );

  const linksFormObjectListChangeHandler = useCallback(
    (args) => (event) => {
      const { objectList, index } = { ...args };

      let updatedForm = fieldChangeHandler(args, event);

      let arrayObjectForm = [...objectList];
      let updatedList = { ...arrayObjectForm[index] };
      updatedList = updatedForm;
      arrayObjectForm[index] = updatedList;

      updatedForm = arrayObjectForm;

      setLinks(updatedForm);
    },
    [fieldChangeHandler]
  );

  const contactFormObjectChangeHandler = useCallback(
    (args) => (event) => {
      const updatedForm = fieldChangeHandler(args, event);

      setContactForm(updatedForm);
    },
    [fieldChangeHandler]
  );

  const companyFormObjectChangeHandler = useCallback(
    (args) => (event) => {
      const updatedForm = fieldChangeHandler(args, event);

      setCompanyForm(updatedForm);
    },
    [fieldChangeHandler]
  );

  const newLinkObjectChangeHandler = useCallback(
    (args) => (event) => {
      const updatedForm = fieldChangeHandler(args, event);

      setNewLink(updatedForm);
    },
    [fieldChangeHandler]
  );

  const newDetailObjectChangeHandler = useCallback(
    (args) => (event) => {
      const updatedForm = fieldChangeHandler(args, event);

      setNewDetail(updatedForm);
    },
    [fieldChangeHandler]
  );

  const removeLinkObjectHandler = useCallback(
    (args) => () => {
      const { objectList, index } = {
        ...args,
      };

      let updatedForm = [...objectList];
      updatedForm.splice(index, 1);

      setLinks(updatedForm);
    },
    []
  );

  const removeDetailObjectHandler = useCallback(
    (args) => () => {
      const { objectList, index } = {
        ...args,
      };

      let updatedForm = [...objectList];
      updatedForm.splice(index, 1);

      setDetails(updatedForm);
    },
    []
  );

  const addLinkObjectHandler = (args) => () => {
    const { objectList, newState, mapping } = {
      ...args,
    };
    const updatedForm = [...objectList];
    let copiedNewState = JSON.parse(JSON.stringify(newState));

    const keyValues = Object.keys(copiedNewState).map((key) => {
      // newTitle
      const newKey = mapping[key] || key; // title
      return { [newKey]: copiedNewState[key] }; // newTitle => title
    });

    let copiedKeyValues = Object.assign({}, ...keyValues);

    for (const key in copiedKeyValues) {
      const splitLabel = copiedKeyValues[key].label.split(' '); // label:"New Link Title" => New Link Title
      splitLabel.splice(0, 1); // New,Link,Title => Link,Title
      const recreateLabel = splitLabel.join(' '); // "Link Title"
      copiedKeyValues[key].label = recreateLabel;
    }

    updatedForm.push(copiedKeyValues);

    setLinks(updatedForm);

    let copiedNewState2 = JSON.parse(JSON.stringify(newState));

    for (const key in copiedNewState2) {
      copiedNewState2[key].value = '';
    }

    setNewLink(copiedNewState2);
  };

  const addDetailObjectHandler = (args) => () => {
    const { objectList, newState, mapping } = {
      ...args,
    };
    const updatedForm = [...objectList];
    let copiedNewState = JSON.parse(JSON.stringify(newState));

    const keyValues = Object.keys(copiedNewState).map((key) => {
      // newTitle
      const newKey = mapping[key] || key; // title
      return { [newKey]: copiedNewState[key] }; // newTitle => title
    });

    let copiedKeyValues = Object.assign({}, ...keyValues);

    for (const key in copiedKeyValues) {
      const splitLabel = copiedKeyValues[key].label.split(' '); // label:"New Link Title" => New Link Title
      splitLabel.splice(0, 1); // New,Link,Title => Link,Title
      const recreateLabel = splitLabel.join(' '); // "Link Title"
      copiedKeyValues[key].label = recreateLabel;
    }

    updatedForm.push(copiedKeyValues);

    setDetails(updatedForm);

    let copiedNewState2 = JSON.parse(JSON.stringify(newState));

    for (const key in copiedNewState2) {
      copiedNewState2[key].value = '';
    }

    setNewDetail(copiedNewState2);
  };

  const cardChangeHandler = (cardFacade) => (event) => {
    let pickedFile;

    // let fileIsValid =
    //   cardFacade === 'front' ? frontCardIsValid : backCardIsValid;
    if (event.target.files && event.target.files.length === 1) {
      pickedFile = event.target.files[0];
      cardFacade === 'front'
        ? setFrontCard(pickedFile)
        : setBackCard(pickedFile);

      cardFacade === 'front'
        ? setFrontCardIsValid(true)
        : setBackCardIsValid(true);
      // fileIsValid = true;
    } else {
      cardFacade === 'front'
        ? setFrontCardIsValid(false)
        : setBackCardIsValid(false);
      // fileIsValid = false;
    }

    cardFacade === 'front'
      ? setFrontCardIsLoading(false)
      : setBackCardIsLoading(false);
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };

  const totalSteps = () => {
    return steps.length;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const stepHandler = (step) => () => {
    setActiveStep(step);
  };

  const stepBackHandler = () => {
    setActiveStep(activeStep - 1);
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const stepNextHandler = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? steps.findIndex((step, i) => !(i in completed))
        : activeStep + 1;
    setActiveStep(newActiveStep);
  };

  const stepCompleteHandler = () => {
    const newCompleted = { ...completed };
    newCompleted[activeStep] = true;
    setCompleted(newCompleted);
    stepNextHandler();
  };

  const steps = [
    'Contact info',
    'Company info',
    'Card info',
    'Links info',
    'Details info',
  ];

  const CompleteButton = (props) => {
    const { infoIsValid } = props;

    return (
      activeStep !== steps.length &&
      (completed[activeStep] ? (
        <LoadingButton
          className={classes.completeButton}
          startIcon={
            <SvgIcon>
              <path d={checkIcon.path} />
            </SvgIcon>
          }
          disabled
          color="secondary"
          onClick={stepNextHandler}
          pendingPosition="start"
          variant="contained"
        >
          Completed
        </LoadingButton>
      ) : (
        <LoadingButton
          className={classes.completeButton}
          startIcon={
            <SvgIcon>
              {completedSteps() === totalSteps() - 1 ? (
                <path d={plusIcon.path} />
              ) : (
                <path d={checkIcon.path} />
              )}
            </SvgIcon>
          }
          disabled={!infoIsValid}
          color="secondary"
          onClick={stepCompleteHandler}
          pendingPosition="start"
          variant="contained"
        >
          {completedSteps() === totalSteps() - 1 ? 'Create' : 'Complete'}
        </LoadingButton>
      ))
    );
  };

  if (!state.isAuthenticated) {
    return <Redirect to="signin" />;
  }

  return (
    <Grid
      container
      direction="row"
      justifyContent="center"
      alignItems="flex-start"
    >
      {/* <Grid item style={{ width: 600, height: 400, border: '3px solid white' }}>
        <Card3d data={data} />
      </Grid> */}
      <Paper className={classes.stepperContainer}>
        <Grid
          item
          container
          direction="column"
          justifyContent="flex-start"
          alignItems="center"
        >
          <Grid item>
            {isSmallDevice ? (
              <MobileStepper
                steps={totalSteps()}
                activeStep={activeStep}
                backButton={
                  <LoadingButton
                    startIcon={
                      <SvgIcon>
                        <path d={leftArrowIcon.path} />
                      </SvgIcon>
                    }
                    color="secondary"
                    disabled={activeStep === 0}
                    onClick={stepBackHandler}
                    pendingPosition="start"
                    variant="contained"
                  >
                    Back
                  </LoadingButton>
                }
                nextButton={
                  <LoadingButton
                    startIcon={
                      <SvgIcon>
                        <path d={rightArrowIcon.path} />
                      </SvgIcon>
                    }
                    color="secondary"
                    disabled={isLastStep()}
                    onClick={stepNextHandler}
                    pendingPosition="start"
                    variant="contained"
                  >
                    Next
                  </LoadingButton>
                }
              />
            ) : (
              <Stepper
                nonLinear
                activeStep={activeStep}
                orientation="horizontal"
                alternativeLabel={isLargeDevice ? false : true}
                className={classes.stepperHeader}
              >
                {steps.map((label, index) => (
                  <Step key={label} completed={completed[index]}>
                    <StepButton onClick={stepHandler(index)}>
                      {label}
                    </StepButton>
                  </Step>
                ))}
              </Stepper>
            )}
          </Grid>
          <Grid item className={classes.stepperContent}>
            {
              {
                0: (
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <RestructureFormObject
                      formObject={contactForm}
                      formChangeHandler={contactFormObjectChangeHandler}
                    />
                    <Grid item>
                      <CompleteButton infoIsValid={contactInfoIsValid} />
                    </Grid>
                  </Grid>
                ),
                1: (
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <RestructureFormObject
                      formObject={companyForm}
                      formChangeHandler={companyFormObjectChangeHandler}
                    />
                    <Grid item>
                      <CompleteButton infoIsValid={companyInfoIsValid} />
                    </Grid>
                  </Grid>
                ),
                2: (
                  <Grid
                    container
                    direction="column"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <Grid item>
                      <input
                        id="front-card"
                        ref={frontCardRef}
                        style={{ display: 'none' }}
                        type="file"
                        accept=".jpg,.png,.jpeg"
                        onChange={cardChangeHandler('front')}
                      />
                    </Grid>
                    <Grid item className={classes.stepperInnerContent}>
                      <Paper className={classes.cardContainer}>
                        {previewFrontCard && (
                          <img
                            src={previewFrontCard}
                            alt="Preview"
                            className={classes.cardContainer}
                          />
                        )}
                        {!previewFrontCard && (
                          <Grid
                            container
                            justifyContent="center"
                            alignItems="center"
                            style={{ width: '100%', height: '100%' }}
                          >
                            <p>Please pick front card image</p>
                          </Grid>
                        )}
                      </Paper>
                    </Grid>
                    <Grid item className={classes.stepperInnerContent}>
                      <LoadingButton
                        startIcon={
                          <SvgIcon>
                            <path d={uploadIcon.path} />
                          </SvgIcon>
                        }
                        color="secondary"
                        onClick={pickCardHandler('front')}
                        pendingPosition="start"
                        variant="outlined"
                        pending={backCardIsLoading}
                      >
                        Front Card
                      </LoadingButton>
                    </Grid>
                    <Grid item>
                      <input
                        id="back-card"
                        ref={backCardRef}
                        style={{ display: 'none' }}
                        type="file"
                        accept=".jpg,.png,.jpeg"
                        onChange={cardChangeHandler('back')}
                      />
                    </Grid>
                    <Grid item className={classes.stepperInnerContent}>
                      <Paper className={classes.cardContainer}>
                        {previewBackCard && (
                          <img
                            src={previewBackCard}
                            alt="Preview"
                            className={classes.cardContainer}
                          />
                        )}
                        {!previewBackCard && (
                          <Grid
                            container
                            justifyContent="center"
                            alignItems="center"
                            style={{ width: '100%', height: '100%' }}
                          >
                            <p>Please pick back card image</p>
                          </Grid>
                        )}
                      </Paper>
                    </Grid>
                    <Grid item className={classes.stepperInnerContent}>
                      <LoadingButton
                        startIcon={
                          <SvgIcon>
                            <path d={uploadIcon.path} />
                          </SvgIcon>
                        }
                        color="secondary"
                        onClick={pickCardHandler('back')}
                        pendingPosition="start"
                        variant="outlined"
                        pending={backCardIsLoading}
                      >
                        Back Card
                      </LoadingButton>
                    </Grid>
                    <Grid item>
                      <CompleteButton infoIsValid={cardInfoIsValid} />
                    </Grid>
                  </Grid>
                ),
                3: (
                  <Grid container direction="column" alignItems="center">
                    {links.length > 0 ? (
                      <FormObjectList
                        objectList={links}
                        formChangeHandler={linksFormObjectListChangeHandler}
                        removeFormObjectHandler={removeLinkObjectHandler}
                        flowType="flex"
                      />
                    ) : null}
                    <Grid
                      container
                      direction={isLargeDevice ? 'row' : 'column'}
                      justifyContent="center"
                      alignItems={isLargeDevice ? null : 'center'}
                    >
                      <RestructureFormObject
                        formObject={newLink}
                        formChangeHandler={newLinkObjectChangeHandler}
                      />
                      <Grid item>
                        {isLargeDevice ? (
                          <IconButton
                            className={classes.iconButton}
                            aria-label="add"
                            color="secondary"
                            onClick={addLinkObjectHandler({
                              objectList: links,
                              newState: newLink,
                              mapping: linksMapping,
                            })}
                          >
                            <SvgIcon>
                              <path d={plusIcon.path} />
                            </SvgIcon>
                          </IconButton>
                        ) : (
                          <LoadingButton
                            aria-label="add"
                            color="secondary"
                            onClick={addLinkObjectHandler({
                              objectList: links,
                              newState: newLink,
                              mapping: linksMapping,
                            })}
                            startIcon={
                              <SvgIcon>
                                <path d={plusIcon.path} />
                              </SvgIcon>
                            }
                            pendingPosition="start"
                            variant="outlined"
                          >
                            ADD
                          </LoadingButton>
                        )}
                      </Grid>
                    </Grid>
                    <Grid item>
                      <CompleteButton infoIsValid={linksInfoIsValid} />
                    </Grid>
                  </Grid>
                ),
                4: (
                  <Grid container direction="column" alignItems="center">
                    {details.length > 0 ? (
                      <FormObjectList
                        objectList={details}
                        formChangeHandler={detailsFormObjectListChangeHandler}
                        removeFormObjectHandler={removeDetailObjectHandler}
                        flowType="column"
                      />
                    ) : null}
                    <Grid
                      container
                      direction="column"
                      justifyContent="center"
                      alignItems="center"
                    >
                      <RestructureFormObject
                        formObject={newDetail}
                        formChangeHandler={newDetailObjectChangeHandler}
                      />
                      <Grid item>
                        <LoadingButton
                          aria-label="add"
                          color="secondary"
                          onClick={addDetailObjectHandler({
                            objectList: details,
                            newState: newDetail,
                            mapping: detailsMapping,
                          })}
                          startIcon={
                            <SvgIcon>
                              <path d={plusIcon.path} />
                            </SvgIcon>
                          }
                          pendingPosition="start"
                          variant="outlined"
                        >
                          ADD
                        </LoadingButton>
                      </Grid>
                    </Grid>
                    <Grid item>
                      <CompleteButton infoIsValid={DetailsInfoIsValid} />
                    </Grid>
                  </Grid>
                ),
              }[activeStep]
            }
          </Grid>

          {allStepsCompleted() ? (
            <p>All Steps are Completed</p>
          ) : (
            <Grid container item>
              {isSmallDevice ? null : (
                <Grid
                  item
                  container
                  style={{ width: '100%', margin: 12 }}
                  justifyContent="space-between"
                >
                  <Grid item>
                    <LoadingButton
                      startIcon={
                        <SvgIcon>
                          <path d={leftArrowIcon.path} />
                        </SvgIcon>
                      }
                      color="secondary"
                      disabled={activeStep === 0}
                      onClick={stepBackHandler}
                      pendingPosition="start"
                      variant="contained"
                    >
                      Back
                    </LoadingButton>
                  </Grid>
                  <Grid item>
                    <LoadingButton
                      startIcon={
                        <SvgIcon>
                          <path d={rightArrowIcon.path} />
                        </SvgIcon>
                      }
                      color="secondary"
                      disabled={isLastStep()}
                      onClick={stepNextHandler}
                      pendingPosition="start"
                      variant="contained"
                    >
                      Next
                    </LoadingButton>
                  </Grid>
                </Grid>
              )}
            </Grid>
          )}
        </Grid>
      </Paper>
    </Grid>
  );
};

export default CardDetails;
