import React from 'react';
import QRCode from 'react-qr-code';
import {
  IconButton,
  Dialog,
  DialogContent,
  Link,
  DialogTitle,
  Typography,
} from '@material-ui/core';
import {
  Language as LanguageIcon,
  Bookmark as BookmarkIcon,
  Share as ShareIcon,
  ExpandMore as ExpandMoreIcon,
  Phone as PhoneIcon,
  Email as EmailIcon,
  Message as MessageIcon,
  QrCode2 as QrCode2Icon,
  Cancel as CancelIcon,
  AddCircle as AddCircleIcon,
} from '@material-ui/icons';
import { makeStyles, withStyles } from '@material-ui/core/styles';

import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));

const styles = (theme) => ({
  dialogCloseButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  dialogAddButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
});

const CustomDialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, onAdd, ...rest } = props;
  return (
    <DialogTitle disableTypography {...rest}>
      <Typography variant="h6" component="div">
        {children}
      </Typography>
      {onAdd ? (
        <IconButton
          aria-label="close"
          onClick={onAdd}
          className={classes.dialogAddButton}
        >
          <AddCircleIcon />
        </IconButton>
      ) : null}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          className={classes.dialogCloseButton}
        >
          <CancelIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
});

const CardActionLinks = (props) => {
  const classes = useStyles();

  return (
    <>
      <Link
        href={props.data.website}
        underline="none"
        target="_blank"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="website">
          <LanguageIcon />
        </IconButton>
      </Link>
      <IconButton aria-label="bookmark" onClick={props.bookmarkHandler}>
        <BookmarkIcon />
      </IconButton>
      <Dialog
        open={props.bookmarkIsOpen}
        aria-labelledby="bookmark-dialog-title"
        onClose={props.bookmarkHandler}
      >
        <CustomDialogTitle
          id="bookmark-dialog-title"
          onClose={props.bookmarkHandler}
          onAdd={props.bookmarkAddHandler}
        >
          Bookmark
        </CustomDialogTitle>
        <DialogContent dividers>
          <QRCode value={props.data.initLink} />
        </DialogContent>
      </Dialog>
      <Link
        href={`tel:+${props.data.contactPerson.contactNumber}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="phone">
          <PhoneIcon />
        </IconButton>
      </Link>
      <Link
        href={`mailto:${props.data.contactPerson.contactEmail}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="email">
          <EmailIcon />
        </IconButton>
      </Link>
      <Link
        href={`sms:/${props.data.contactPerson.contactNumber}/?body=/${props.data.contactPerson.smsBody}/`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="message">
          <MessageIcon />
        </IconButton>
      </Link>
      <IconButton aria-label="share" onClick={props.shareHandler}>
        <ShareIcon />
      </IconButton>
      <Dialog
        open={props.shareIsOpen}
        aria-labelledby="share-dialog-title"
        onClose={props.shareHandler}
      >
        <CustomDialogTitle id="share-dialog-title" onClose={props.shareHandler}>
          Share
        </CustomDialogTitle>
        <DialogContent dividers>
          <QRCode value={props.data.initLink} />
        </DialogContent>
      </Dialog>
      <IconButton aria-label="qr-code" onClick={props.qrCodeHandler}>
        <QrCode2Icon />
      </IconButton>
      <Dialog
        open={props.qrCodeIsOpen}
        aria-labelledby="qr-code-dialog-title"
        onClose={props.qrCodeHandler}
      >
        <CustomDialogTitle
          id="qr-code-dialog-title"
          onClose={props.qrCodeHandler}
        >
          QR Code
        </CustomDialogTitle>
        <DialogContent dividers>
          <QRCode value={props.data.initLink} />
        </DialogContent>
      </Dialog>
      <IconButton
        className={clsx(classes.expand, {
          [classes.expandOpen]: props.expanded,
        })}
        onClick={props.handleExpandClick}
        aria-expanded={props.expanded}
        aria-label="show more"
      >
        <ExpandMoreIcon />
      </IconButton>
    </>
  );
};

export default CardActionLinks;
