import React, { useState, useCallback, useEffect } from 'react';
import { SvgIcon, Grid, Typography, Paper } from '@material-ui/core';
import LoadingButton from '@material-ui/lab/LoadingButton';
import { makeStyles } from '@material-ui/core/styles';
import { Link as RouterLink, Redirect } from 'react-router-dom';

import {
  closeIcon,
  visibilityOffIcon,
  visibilityOnIcon,
  loginIcon,
  lockQuestionIcon,
  swapIcon,
} from '../../assets/svg/SvgIcons';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import { fieldsIsValidHandler } from '../helper/fieldsIsValidHandler';
import RestructureFormObject from '../helper/RestructureFormObject';
import { useStore } from '../../hooks/store';
import { User } from '../Demo/User';

const useStyles = makeStyles((theme) => ({
  inputContainer: {
    maxWidth: theme.breakpoints.values.sm,
    [theme.breakpoints.up('sm')]: {
      borderRadius: theme.spacing(1),
      marginTop: theme.spacing(3),
    },
    width: theme.breakpoints.values.sm,
    padding: theme.spacing(3),
  },
  buttonSpacing: {
    marginTop: theme.spacing(1),
  },
}));

const Registration = (props) => {
  const [state, dispatch] = useStore(true);
  const [isPendingSignIn, setIsPendingSignIn] = useState(false);
  const [isPendingRequestPassword, setIsPendingRequestPassword] = useState(
    false
  );
  const [isPendingChangeType, setIsPendingChangeType] = useState(false);
  const [enteredCredential, setEnteredCredential] = useState({
    email: {
      value: '',
      name: 'email',
      label: 'Email',
      type: 'email',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        isEmail: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeIcon,
      },
    },
    password: {
      value: '',
      name: 'password',
      label: 'Password',
      type: 'password',
      placeHolder: '',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'toggle',
        on: visibilityOnIcon,
        off: visibilityOffIcon,
        isOn: false,
      },
    },
  });
  const [enteredCredentialIsValid, setEnteredCredentialIsValid] = useState(
    false
  );

  const classes = useStyles();

  const signInHandler = () => {
    if (
      enteredCredential.email.value === 'markzukerberg@faceboook.com' &&
      enteredCredential.password.value === '123'
    ) {
      dispatch('SIGN_IN', User);
    } else {
      alert('Invalid Credential');
    }
  };

  const forgetPasswordHandler = () => {
    setIsPendingRequestPassword(true);
    setTimeout(() => {
      setIsPendingRequestPassword(false);
    }, 1000);
  };

  const enteredCredentialChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setEnteredCredential(updatedForm);
  };

  const enteredCredentialIsValidHandler = useCallback((args) => {
    const fieldsIsValid = fieldsIsValidHandler(args);

    setEnteredCredentialIsValid(fieldsIsValid);
  }, []);

  useEffect(() => {
    enteredCredentialIsValidHandler({ formObject: enteredCredential });
  }, [enteredCredential, enteredCredentialIsValidHandler]);

  if (state.isAuthenticated) {
    return <Redirect to="/" />;
  }

  return (
    <Grid container direction="row" justifyContent="center" alignItems="center">
      <Paper className={classes.inputContainer}>
        <Grid
          container
          direction="column"
          justifyContent="flex-start"
          alignItems="center"
        >
          <Typography variant="h4">Sign In</Typography>
          <Grid item>
            <RestructureFormObject
              formObject={enteredCredential}
              formChangeHandler={enteredCredentialChangeHandler}
            />
          </Grid>

          <Grid item style={{ display: 'flex' }}>
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={loginIcon.path} />
                </SvgIcon>
              }
              color="secondary"
              onClick={signInHandler}
              pending={isPendingSignIn}
              disabled={!enteredCredentialIsValid}
              pendingPosition="start"
              variant="contained"
              className={classes.buttonSpacing}
              type="submit"
            >
              Sign In
            </LoadingButton>
          </Grid>
          <Grid item style={{ display: 'flex' }}>
            <LoadingButton
              component={RouterLink}
              to={'/signup'}
              startIcon={
                <SvgIcon>
                  <path d={swapIcon.path} />
                </SvgIcon>
              }
              color="secondary"
              pending={isPendingChangeType}
              pendingPosition="start"
              variant="outlined"
              className={classes.buttonSpacing}
            >
              Switch to Register
            </LoadingButton>
          </Grid>

          <Grid item style={{ display: 'flex' }}>
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={lockQuestionIcon.path} />
                </SvgIcon>
              }
              color="secondary"
              onClick={forgetPasswordHandler}
              pending={isPendingRequestPassword}
              pendingPosition="start"
              variant="outlined"
              className={classes.buttonSpacing}
            >
              Forget Password
            </LoadingButton>
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  );
};

export default Registration;
