import React from 'react';
import {
  ListItem,
  SvgIcon,
  ListItemIcon,
  ListItemText,
  useMediaQuery,
} from '@material-ui/core';

import { Link as RouterLink } from 'react-router-dom';

const VerticalNavItems = (props) => {
  const {
    verticalNavItemList,
    verticalNavItemValue,
    isActiveStyle,
    routeHistory,
    toggleDrawer,
  } = props;

  const isExtraSmallDevice = useMediaQuery((theme) =>
    theme.breakpoints.down('sm')
  );

  return verticalNavItemList.map((linkItem) => (
    <ListItem
      button
      key={`${linkItem.label}`}
      selected={routeHistory === `/${linkItem.linkTo}`}
      component={RouterLink}
      to={`/${linkItem.linkTo}`}
      style={isActiveStyle(routeHistory, `/${linkItem.linkTo}`)}
      onClick={toggleDrawer}
      dense={!isExtraSmallDevice}
    >
      <ListItemIcon>
        <SvgIcon style={isActiveStyle(routeHistory, `/${linkItem.linkTo}`)}>
          <path d={linkItem.svgPath} />
        </SvgIcon>
      </ListItemIcon>
      <ListItemText primary={linkItem.name} />
    </ListItem>
  ));
};

export default VerticalNavItems;
