import React, { useState } from 'react';
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  SvgIcon,
  useMediaQuery,
} from '@material-ui/core';

import { logoutIcon } from '../../assets/svg/SvgIcons';
import { useStore } from '../../hooks/store';

const SignOut = (props) => {
  const { onClickHandler } = props;
  const isExtraSmallDevice = useMediaQuery((theme) =>
    theme.breakpoints.down('sm')
  );
  const [state, dispatch] = useStore(true);

  const [signOut, setSignOut] = useState({
    name: 'Sign Out',
    label: 'signout',
    svgPath: logoutIcon.path,
    type: 'authenticated',
  });

  const signOutHandler = () => {
    onClickHandler();
    dispatch('SIGN_OUT');
  };

  return (
    <ListItem
      button
      divider
      dense={!isExtraSmallDevice}
      onClick={signOutHandler}
    >
      <ListItemIcon>
        <SvgIcon>
          <path d={signOut.svgPath} />
        </SvgIcon>
      </ListItemIcon>
      <ListItemText primary={signOut.name} />
    </ListItem>
  );
};

export default SignOut;
