import React, { useState } from 'react';

import CardList from '../Card/CardList';
import { FullList } from '../Demo/FullList';

const LatestCard = () => {
  const [cardDataList, setCardDataList] = useState(FullList);
  const [cardIsExpanded, setCardIsExpanded] = useState(false);

  return (
    <CardList cardDataList={cardDataList} cardIsExpanded={cardIsExpanded} />
  );
};

export default LatestCard;
